<template>
	<view class="" style="position: relative;">
		<load ref="suba"></load>
		<image class="bgc-picture1" src="../../static/fristPage/bgcL.png" mode="widthFix"></image>
		<view class="" style="background-color: #2b95d0; position: fixed; bottom: 0; z-index: -2; height: 100vh; width: 750rpx;"></view>		
		<view class="" style="height: 290rpx;"></view>
		<view class="" v-if="subscrFlag" style="">
			<view v-if="false" class="" style="position: absolute; top: 130rpx; left: 78rpx;"
			hover-class="hover1" hover-start-time=0 hover-stay-time=150 >
				<image @click="backRegister" src="../../static/Index/fanHui.png" mode="widthFix" style="width: 25rpx;"></image>
			</view>  
			<view class="bgc_FFF" style="margin: 0 78rpx; border-radius: 51rpx; height: 900rpx;">
				<view class="" style="height: 70rpx;"></view>
				<view class="f49rpx ta-c color-H0f649c fw800 lsp-5" style="">Subscribe Today</view>
				<view class="ta-c  f33rpx" :style="hasActive ? 'line-height: 36rpx; color: #0f649c;':'color: #515350; line-height: 36rpx;'"
					style="margin: 35rpx 34rpx 26rpx; ">
					{{hasActive ? actText1:hasActText1}} 
				</view>
				<view class="f33rpx ta-c" 
					:style="hasActive ? 'color: #0f649c; line-height: 42rpx;':'color: #515350; line-height: 36rpx;'"
					style="">
					{{hasActive ? actText2:hasActText2}}
					<view v-if="hasActive" class="fw800" style="color: #0f649c;">www.tekygo.com.</view>
				</view>
				<view class="FFF f33rpx fw800 ta-c bgc-o-r" @click="goSubscribe"
				hover-class="hover_a24" hover-start-time=0 hover-stay-time=150
				style="margin: 33rpx 43rpx 38rpx 48rpx; height: 80rpx; line-height: 80rpx; border-radius: 40rpx;"
				>Subscribe
				</view>
				<view class="ta-c f33rpx color-H0f649c" style="line-height: 42rpx; margin: 0 30rpx 40rpx;">
					Or browse for free with limited access.
				</view>
				<view @click="goIndex" class="f33rpx color-H0f649c fw800 ta-c"
					hover-class="hover" hover-start-time=0 hover-stay-time=150
					style="text-decoration: underline; white-space: nowrap">
					Free Access
				</view> 
				<view class=""></view>
			</view>
		</view>
	
		<view class="" v-if="PaymentFlag">
			<view class="" style="position: absolute; top: 130rpx; left: 78rpx;"
			hover-class="hover1" hover-start-time=0 hover-stay-time=150 >
					<image @click="backSubscr" src="../../static/Index/fanHui.png" mode="widthFix" style="width: 25rpx;"></image>
			</view>
			<view class="bgc_FFF" style="margin: 0 78rpx; border-radius: 51rpx; height: 619rpx;">
				<view class="" style="height: 70rpx;"></view>
				<view class="f49rpx ta-c color-H0f649c fw800 lsp-5" style="">Payment Verification</view>
				<view class="ta-c color-H0f649c f33rpx" style="margin: 35rpx 50rpx 26rpx; line-height: 42rpx;">
					If your payment was 
					successfully completed, click 
					below to verify and connect 
					your accounts, or cancel.
				</view>
				<view class="FFF f33rpx fw800 ta-c bgc-o-r" @click="goSubCon"
				hover-class="hover_a24" hover-start-time=0 hover-stay-time=150
				style="margin: 33rpx 43rpx 38rpx 48rpx; height: 80rpx; line-height: 80rpx; border-radius: 40rpx;"
				>Verify
				</view>
				<view class="FFF f33rpx fw800 ta-c bgc-o-r" @click="backSubscr"
				hover-class="hover_a24" hover-start-time=0 hover-stay-time=150
				style="margin: 33rpx 43rpx 38rpx 48rpx; height: 80rpx; line-height: 80rpx; border-radius: 40rpx;"
				>Cancel
				</view>
			</view>
		</view>
		
		<view class="" v-if="SunConFlag">
			<view class="" style="position: absolute; top: 130rpx; left: 78rpx;"
			hover-class="hover1" hover-start-time=0 hover-stay-time=150 >
				<image @click="backPayment" src="../../static/Index/fanHui.png" mode="widthFix" style="width: 25rpx;"></image>
			</view>
			<view class="bgc_FFF" style="margin: 0 78rpx; border-radius: 51rpx; height: 1000rpx;">
				<view class="" style="height: 70rpx;"></view>
				<view class=" ta-c color-H0f649c fw800 lsp-5" style="font-size: 45rpx;"> Subscription Connecting</view>
				<view class="df-jcc-aic" style="margin: 25rpx 0 16rpx;">
					<view class="load-box" style=" z-index: 10;">
						<image src="../../static/fristPage/loading.png" class="loading"></image>
					</view>
				</view>
				<view class="ta-c f33rpx color-H0f649c">Processing...</view>
				<view class="ta-c color-H0f649c f33rpx" style="margin: 20rpx 34rpx 20rpx; line-height: 42rpx;">
					TekyGo! is working on connecting your subscription to the app.
				</view>
				<view class="f33rpx ta-c" style="color: #0f649c; line-height: 42rpx; margin: 0 20rpx;">
					<text class="fw800" style="margin-right: 10rpx;">Note: </text>This may take a few moments. 
				</view> 
				<view class="ta-c color-H0f649c f33rpx" style="margin: 30rpx 34rpx 30rpx; line-height: 42rpx;">
					If you are experiencing issues, re-verify below or contact us.
				</view>
				<view class="FFF f33rpx fw800 ta-c bgc-o-r" @click="goReVerify"
				hover-class="hover_a24" hover-start-time=0 hover-stay-time=150
				style="margin: 33rpx 43rpx 38rpx 48rpx; height: 80rpx; line-height: 80rpx; border-radius: 40rpx;"
				>Re-Verify
				</view>
				<view class="df-jcc-aic">
					<view @click="goContactUS" class="f33rpx color-H0f649c fw800" 
					hover-class="hover" hover-start-time=0 hover-stay-time=150
					style="border-bottom: 1rpx solid #0f649c; line-height: 33rpx;">
						Contact US
					</view>
				</view>
			</view>
		</view>
	
		<view class="" v-if="ContactUsFlag">
			<view class="" style="position: absolute; top: 130rpx; left: 78rpx;"
			hover-class="hover1" hover-start-time=0 hover-stay-time=150 >
				<image @click="backSunCon" src="../../static/Index/fanHui.png" mode="widthFix" style="width: 25rpx;"></image>
			</view>
			<view class="bgc_FFF" style="margin: 0 78rpx; border-radius: 51rpx; height: 900rpx;">
				<view class="" style="height: 70rpx;"></view>
				<view class=" ta-c color-H0f649c fw800" style="font-size: 43rpx;"> Need Assistance?</view>
				<view class="ta-c color-H0f649c f33rpx" style="margin: 50rpx 50rpx 42rpx; line-height: 42rpx;">
					 If you need further assistance, 
					 please connect to our customer 
					 service team through phone or email.
				</view>
				<view class="ta-c color-H0f649c fw800 f40rpx" style="">
					Email:
				</view>
				<view class="ta-c f33rpx" 
				style="color: #eb5120; margin: 0 0 50rpx; line-height: 42rpx; letter-spacing: 2rpx;">{{email}}</view>
				<view class="ta-c color-H0f649c fw800 f40rpx" style="margin: ;">
					Call Number:
				</view>
				<view class="ta-c f33rpx" style="color: #eb5120; line-height: 42rpx; margin: 0rpx 0 50rpx;">
					+{{phone}}</view>
				<view class="ta-c color-H0f649c f33rpx" 
				style="margin: 0rpx 34rpx 30rpx; line-height: 42rpx;">
					If you’d like to back out, please click below.
				</view>
				<view class="FFF f33rpx fw800 ta-c bgc-o-r" @click="gosubscr"
				hover-class="hover_a24" hover-start-time=0 hover-stay-time=150
				style="margin: 33rpx 43rpx 38rpx 48rpx; height: 80rpx; line-height: 80rpx; border-radius: 40rpx;"
				>Cancel
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default{
		name: 'loading',
		data(){
			return{
				subscrFlag:true,
				PaymentFlag:false,
				SunConFlag:false,
				ContactUsFlag:false,
				email:'hello@tekygo.com',
				phone:'1 8## ### ###',
				ifBack:false,
				hasActive:false,
				actText1:`A subscription to TekyGo! means endless ,
									access to a whole new world filled with 
									educational games, special events, and more! `,
				actText2:`Press subscribe and you’ll be redirected to our website at `,
				hasActText1:` Congratulations! You just unlocked 
											your FREE 2 month Premium TekyGo! 
											Subscription. We just need a few more 
											details to get started. `,
				hasActText2:`	Press the subscribe button below 
											and you will be redirected to the 
											TekyGo! website to complete your 
											registration.`						
			}
		},
		onLoad(e) {
			// if(e.ss){
			// 	this.ifBack = true
			// }else{
			// 	this.ifBack = false
			// }
			// console.log(this.ifBack);
		},
		created() {
			let s = uni.getStorageSync('hasActive')
			if(s){
				this.hasActive = true
			}else{
				this.hasActive = false
			}
			console.log(this.hasActive); 
		},
		methods:{
			gosubscr(){
				this.ContactUsFlag = false
				this.SunConFlag = false
				this.PaymentFlag = false
				this.subscrFlag = true
			}, 
			goReVerify(){
				
			},
			backSunCon(){
				this.ContactUsFlag = false
				this.SunConFlag = true
			},
			//3
			goContactUS(){
				this.SunConFlag = false
				this.ContactUsFlag = true
			},
			backPayment(){
				this.PaymentFlag = true
				this.SunConFlag = false
			},
			//2
			async goSubCon(){
				this.PaymentFlag = false
				this.SunConFlag = true
				const res = await this.$myRequest({
					url:'/api/sub/validate'
				}) 
				console.log(res);
				if(res.data.succeeded){
					if(res.data.data.active){
						uni.reLaunch({
							url:'./subOk'
						})
					}else{
						uni.reLaunch({
							url:'./subNoOk'
						})
					}
				}
			}, 
			backSubscr(){
				this.subscrFlag = true
				this.PaymentFlag = false
			},

			// 1
			async goSubscribe(){
				this.$refs.suba.openLoad()
				const res = await this.$myRequest({
					url:'/api/sub/get'
				})
				if(res.data.succeeded){
					this.$refs.suba.closeLoad()
					
					plus.runtime.openURL(res.data.data);
					this.subscrFlag = false
					this.PaymentFlag = true
				}

			},
			backRegister(){
				uni.reLaunch({
					url:"../welcomePage/registerWin"
				})
			},
			goIndex(){
				let isBind = uni.getStorageSync('isBind')
				uni.setStorageSync('isLogin',true)
				// console.log('goindex+isBind');
				// console.log(isBind);
				if(isBind){
					uni.switchTab({
						url:"../index/index"
					}) 
				}else{
					uni.redirectTo({
						url:'./Bind'
					})
				}

			}
		}
	}
</script>

<style lang="scss" scoped>
	.bgc-picture1{
		 z-index:-1; 
		 width:100%; 
		 height:100%; 
		 position: fixed;  
		 top:0; 
	}
	
	.load-box {        
		position: relative;    
	}    
	.loading {        
		width: 150rpx;        
		height: 150rpx;        
		display: inline-block;        
		vertical-align: middle;        
		-webkit-animation: a 1s steps(30) infinite;        
		animation: loading 5s steps(30) infinite;        
		// background: transparent url(../../static/fristPage/loading.png) no-repeat;        
		background-size: 100%;    
	}    
	.text {        
		position: absolute;        
		bottom: 13%;        
		left: 50%;        
		transform: translateX(-50%);        
		color: #167BEF;        
		font-size: 28rpx;        
		z-index: 12;    
	}    
	.loading-normal {        
		-webkit-animation: a 1s steps(8) infinite;        
		animation: loading 1s steps(8) infinite;    
	}   
	@keyframes loading {        
		0% {            
			-webkit-transform: rotate(0deg);            
			transform: rotate(0deg);        
		}            
		to {            
			-webkit-transform: rotate(1turn);            
			transform: rotate(1turn);        
		}    
	}

</style>
